import axios from 'axios';

// YYYY-MM-DD
export const formatDateString = (date) => {
  if (!date) return null;
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

// API
export const testApiConnection = async (baseUrl) => {
  try {
    console.log("API:", baseUrl);
    // 
    const timestamp = new Date().getTime();
    
    // 
    const axiosConfig = {
      timeout: 5000,
      // withCredentialsCORS
      headers: {
        'Cache-Control': 'no-cache',
        'Pragma': 'no-cache'
      }
    };
    
    // API
    const response = await axios.get(`${baseUrl}/?_=${timestamp}`, axiosConfig);
    console.log("API:", response.data);
    
    return {
      success: true,
      url: baseUrl,
      message: "API"
    };
  } catch (err) {
    console.error("API:", err);
    
    // 
    // const origin = window.location.origin;
    
    // API
    if (process.env.NODE_ENV === 'production') {
      // API
      const possiblePaths = [
        '/api',      // API
        '/backend',  // 
        '/biorhy',   // 
        '/dress_info' // 
      ];
      
      // 
      const axiosConfig = {
        timeout: 5000,
        headers: {
          'Cache-Control': 'no-cache',
          'Pragma': 'no-cache'
        }
      };
      
      // 
      for (const path of possiblePaths) {
        try {
          // 
          const relativeUrl = path;
          console.log(`: ${relativeUrl}`);
          const timestamp = new Date().getTime();
          const response = await axios.get(`${relativeUrl}/?_=${timestamp}`, axiosConfig);
          console.log(` ${path} :`, response.data);
          
          return {
            success: true,
            url: relativeUrl,
            message: ` ${path} `
          };
        } catch (pathErr) {
          console.error(` ${path} :`, pathErr);
        }
      }
    }
    
    // 
    if (process.env.NODE_ENV !== 'production') {
      // REACT_APP_BACKEND_PORT5201
      const defaultPort = process.env.REACT_APP_BACKEND_PORT || 5201;
      console.log(":", defaultPort);
      // defaultPort
      const alternativePorts = [defaultPort, 5000, 5020, 8000, 8080];
      
      // API
      const apiPaths = ['', '/api', '/biorhythm', '/biorhy'];
      
      for (const port of alternativePorts) {
        for (const path of apiPaths) {
          const altUrl = `http://localhost:${port}${path}`;
          if (altUrl !== baseUrl) {
            try {
              console.log(`: ${altUrl}...`);
              const timestamp = new Date().getTime();
              const altResponse = await axios.get(`${altUrl}/?_=${timestamp}`, {
                timeout: 3000
              });
              console.log(` ${altUrl} :`, altResponse.data);
              
              return {
                success: true,
                url: altUrl,
                message: ` ${altUrl} `
              };
            } catch (altErr) {
              console.error(` ${altUrl} :`, altErr);
            }
          }
        }
      }
    }
    
    return {
      success: false,
      error: ` (${baseUrl}): ${err.message}`,
      // 
      errorDetails: {
        message: err.message,
        code: err.code,
        baseUrl: baseUrl,
        env: process.env.NODE_ENV,
        backendPort: process.env.REACT_APP_BACKEND_PORT || ''
      }
    };
  }
};

