export const fetchBiorhythmData = async (apiBaseUrl, birthDate) => {
  if (!birthDate) {
    return {
      success: false,
      error: ""
    };
  }

  // 
  const axiosConfig = {
    timeout: 8000,
    headers: {
      'Cache-Control': 'no-cache',
      'Pragma': 'no-cache'
    }
  };

  try {
    //  YYYY-MM-DD
    const birthDateStr = typeof birthDate === 'string' 
      ? birthDate 
      : formatDateString(birthDate);
    
    console.log("API:", apiBaseUrl, ":", birthDateStr);
    
    // API
      // const possiblePrefixes = ['', '/api'];
    
    // 
    try {
      // 
      const chartResponse = await axios.get(`${apiBaseUrl}/biorhythm/range`, {
        params: {
          birth_date: birthDateStr,
          days_before: 10,
          days_after: 20
        },
        ...axiosConfig
      });
      
      // 
      const todayResponse = await axios.get(`${apiBaseUrl}/biorhythm/today`, {
        params: {
          birth_date: birthDateStr
        },
        ...axiosConfig
      });
      
      // 10
      const futureDate = new Date();
      futureDate.setDate(futureDate.getDate() + 10);
      const futureDateStr = formatDateString(futureDate);
      
      const futureResponse = await axios.get(`${apiBaseUrl}/biorhythm/date`, {
        params: {
          birth_date: birthDateStr,
          date: futureDateStr
        },
        ...axiosConfig
      });
      
      return {
        success: true,
        rhythmData: chartResponse.data,
        todayData: todayResponse.data,
        futureData: futureResponse.data
      };
    } catch (standardErr) {
      console.error("API:", standardErr);
      
      // API
      try {
        console.log("API...");
        
        // 
        const chartResponse = await axios.get(`${apiBaseUrl}/api/biorhythm`, {
          params: {
            birth_date: birthDateStr,
            days_before: 10,
            days_after: 20
          },
          ...axiosConfig
        });
        
        // 
        const todayResponse = await axios.get(`${apiBaseUrl}/api/biorhythm/today`, {
          params: {
            birth_date: birthDateStr
          },
          ...axiosConfig
        });
        
        // 10
        const futureDate = new Date();
        futureDate.setDate(futureDate.getDate() + 10);
        const futureDateStr = formatDateString(futureDate);
        
        const futureResponse = await axios.get(`${apiBaseUrl}/api/biorhythm/date`, {
          params: {
            birth_date: birthDateStr,
            date: futureDateStr
          },
          ...axiosConfig
        });
        
        return {
          success: true,
          rhythmData: chartResponse.data,
          todayData: todayResponse.data,
          futureData: futureResponse.data
        };
      } catch (oldApiErr) {
        console.error("API:", oldApiErr);
        
        // biorhy
        try {
          console.log("biorhy...");
          
          //  - biorhythm
          const chartResponse = await axios.get(`${apiBaseUrl}/biorhythm/range`, {
            params: {
              birth_date: birthDateStr,
              days_before: 10,
              days_after: 20
            },
            ...axiosConfig
          });
          
          //  - biorhy
          const todayResponse = await axios.get(`${apiBaseUrl}/biorhy/today`, {
            params: {
              birth_date: birthDateStr
            },
            ...axiosConfig
          });
          
          // 10 - biorhy
          const futureDate = new Date();
          futureDate.setDate(futureDate.getDate() + 10);
          const futureDateStr = formatDateString(futureDate);
          
          const futureResponse = await axios.get(`${apiBaseUrl}/biorhy/date`, {
            params: {
              birth_date: birthDateStr,
              date: futureDateStr
            },
            ...axiosConfig
          });
          
          return {
            success: true,
            rhythmData: chartResponse.data,
            todayData: todayResponse.data,
            futureData: futureResponse.data
          };
        } catch (biohryErr) {
          console.error("biorhy:", biohryErr);
          return {
            success: false,
            error: `API: ${biohryErr.message}`
          };
        }
      }
    }
  } catch (err) {
    console.error(":", err);
    
    return {
      success: false,
      error: `: ${err.message}`
    };
  }
};

