export const fetchMayaHistory = async (apiBaseUrl) => {
  // 
  const axiosConfig = {
    timeout: 5000,
    headers: {
      'Cache-Control': 'no-cache',
      'Pragma': 'no-cache'
    }
  };
  
  // API
  const possiblePrefixes = ['', '/api', '/maya'];
  
  // API
  for (const prefix of possiblePrefixes) {
    try {
      const url = `${apiBaseUrl}${prefix}/maya/history`;
      console.log("API:", url);
      const response = await axios.get(url, axiosConfig);
      
      if (response.data && response.data.history) {
        return {
          success: true,
          history: response.data.history
        };
      }
    } catch (err) {
      console.error(` ${prefix} :`, err);
    }
  }
  
  // 
  if (process.env.NODE_ENV === 'production') {
    const relativePaths = ['/maya/history', '/api/maya/history'];
    for (const path of relativePaths) {
      try {
        console.log(":", path);
        const relativeResponse = await axios.get(path, axiosConfig);
        
        if (relativeResponse.data && relativeResponse.data.history) {
          return {
            success: true,
            history: relativeResponse.data.history
          };
        }
      } catch (relativeErr) {
        console.error(` ${path} :`, relativeErr);
      }
    }
  }
  
  // 
  try {
    const mayaHistoryStr = localStorage.getItem('mayaCalendarHistory');
    if (mayaHistoryStr) {
      const history = JSON.parse(mayaHistoryStr);
      if (Array.isArray(history) && history.length > 0) {
        return {
          success: true,
          history: history
        };
      }
    }
  } catch (localErr) {
    console.error(":", localErr);
  }
  
  return {
    success: false,
    error: ""
  };
};

