export const fetchMayaBirthInfo = async (apiBaseUrl, birthDateStr) => {
  if (!birthDateStr) {
    return {
      success: false,
      error: ""
    };
  }

  // 
  const axiosConfig = {
    timeout: 8000,
    headers: {
      'Cache-Control': 'no-cache',
      'Pragma': 'no-cache',
      'Content-Type': 'application/json'
    }
  };
  
  // API (POST)
  try {
    // apiBaseUrl
    const mayaApiUrl = `${apiBaseUrl}/api/maya/birth-info`;
    console.log(`API: ${mayaApiUrl}, : ${birthDateStr}`);
    
    const response = await axios.post(mayaApiUrl, {
      birth_date: birthDateStr
    }, axiosConfig);
    
    if (response.data && response.data.success && response.data.birthInfo) {
      console.log('API:', response.data.birthInfo);
      return {
        success: true,
        birthInfo: response.data.birthInfo
      };
    }
  } catch (mayaApiErr) {
    console.error('API:', mayaApiErr);
  }
  
  // APIAPI
  const possiblePrefixes = ['', '/api', '/maya'];
  
  // API
  for (const prefix of possiblePrefixes) {
    try {
      const url = `${apiBaseUrl}${prefix}/maya/birth`;
      console.log(`(${prefix}):`, `${url}?birth_date=${birthDateStr}`);
      const response = await axios.get(url, {
        params: { birth_date: birthDateStr },
        ...axiosConfig
      });
      
      return {
        success: true,
        birthInfo: response.data
      };
    } catch (err) {
      console.error(` ${prefix} ${birthDateStr}:`, err);
    }
  }
  
  // 
  if (process.env.NODE_ENV === 'production') {
    const relativePaths = ['/maya/birth', '/api/maya/birth', '/maya/date', '/api/maya/date'];
    for (const path of relativePaths) {
      try {
        // 
        const params = path.includes('birth') 
          ? { birth_date: birthDateStr }
          : { date: birthDateStr };
          
        console.log(`${birthDateStr}:`, path);
        const relativeResponse = await axios.get(path, {
          params,
          ...axiosConfig
        });
        
        return {
          success: true,
          birthInfo: relativeResponse.data
        };
      } catch (relativeErr) {
        console.error(` ${path} ${birthDateStr}:`, relativeErr);
      }
    }
  }
  
  // API
  console.log('API');
  return {
    success: false,
    error: "API"
  };
};
